<script>
export default {
	props: [
		'index',
		'title',
		'description',
		'image',
		'brief',
		'dp',
		'color',
		'sound',
	],
	data() {
		return {
			drawer: false,
			audio: '',
			colors: ['#4285F4', '#EA4335', '#FBBC04', '#0F9D58', 'E37400'],
		}
	},
	methods: {
		play(sound) {
			this.audio = sound
			var audio = new Audio(this.audio)
			audio.play()
		},
		stop() {
			this.audio.pause()
		},
	},
}
